import AV from '@/api/AV'

export function createComment (talker, content, hearerId, blogId) {
  const Comment = AV.Object.extend('comment')
  const blog = AV.Object.createWithoutData('Blog', blogId)
  blog.increment('comment', 1)
  blog.save()
  const comment = new Comment()
  comment.set('talker', talker)
  comment.set('content', content)
  comment.set('hearerId', hearerId)
  comment.set('blogId', blogId)
  return comment.save()
}

export function queryComment () {
  return new AV.Query('comment')
}
