import AV from '@/api/AV'

export async function officeDiary (content) {
  const become = await AV.User.become(window.sessionStorage.getItem('token'))
  const Diary = AV.Object.extend('diary')
  const diary = new Diary()
  diary.set('content', content)
  diary.set('email', become.attributes.email)
  return diary.save()
}

export function queryDiary () {
  return new AV.Query('diary')
}

export function setDiary (diaryId) {
  return AV.Object.createWithoutData('diary', diaryId)
}
