<template>
  <transition name = "fade">
    <div id="commonHint"
         v-if="ifShow">{{hint}}</div>
  </transition>
</template>

<script>
export default {
  props: ['hint', 'show'],
  data () {
    return {
      ifShow: this.show
    }
  },
  methods: {
    disappear () {
      this.ifShow = true
      setTimeout(() => {
        this.ifShow = false
      }, 2000)
    }
  },
  watch: {
    show () {
      this.disappear()
    }
  }
}
</script>

<style scoped>
.fade-enter-active, .fade-leave-active {
    transition: opacity 1s
}
.fade-enter, .fade-leave-to /* .fade-leave-active, 2.1.8 版本以下 */ {
    opacity: 0
}
#commonHint {
  background: rgba(78, 77, 77, 0.74);
  height: 100px;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
  text-align: center;
  line-height: 100px;
  color: white;
  font-size: 1.5rem;
  -webkit-user-select: none;
}
</style>
