<template>
  <div id="login">
    <common-hint :hint="hint"
                 :show="ifShow"></common-hint>
    <div class="login-form">
      <h1>Login</h1>
      <form>
        <text-input type='text'
                    name='Email'
                    hint='邮箱输入格式错误'
                    rule='^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$'
                    @inputChange="res => Email = res"></text-input>
        <text-input type='Password'
                    name='password'
                    hint='密码需为6-16位'
                    rule='^.{6,16}$'
                    @inputChange="res => Password = res"></text-input>
        <div class="remember">
          <input type="checkbox"><label>remember me</label>
        </div>
      </form>
      <booth-btn name='SIGN IN'
                 @Submit="goLogin"></booth-btn>
      <h4>
        <router-link to="/home">Go Home</router-link>
        <router-link to="/register">Go Register</router-link>
      </h4>
    </div>
  </div>
</template>

<script>
import boothBtn from '../components/commonBtn/boothBtn.vue'
import TextInput from '../components/commonInput/textInput.vue'
import CommonHint from '../components/commonHints/commonHint'
import { login, emailVerify, PasswordReset } from '../api/_user.js'
import { queryToEmail } from '../api/userinfo.js'
export default {
  data () {
    return {
      Email: '',
      Password: '',
      hint: '',
      ifShow: 0
    }
  },
  components: {
    boothBtn,
    TextInput,
    CommonHint
  },
  methods: {
    goLogin () {
      if (this.Email !== null && this.Password !== null) {
        login(this.Email, this.Password).then(res => {
          if (res.attributes.emailVerified === true) {
            window.sessionStorage.setItem('token', res._sessionToken)
            queryToEmail(res.attributes.email).then(res => {
              window.sessionStorage.setItem('userinfo', JSON.stringify([...res]))
              this.$router.push('/home')
              this.$router.go(0)
            })
            this.ifShow++
            this.hint = '登录成功'
          } else {
            emailVerify()
            this.hint = '您暂时还没有进行邮箱验证，验证信息已经发送至您的QQ邮箱，请先进行邮箱验证'
            this.ifShow++
          }
        }, (error) => {
          if (error) {
            var x = prompt('密码或邮箱输入错误，如需要重置密码请在下方输入正确邮箱地址并确认。我们将会发送邮件至你的QQ邮箱')
            if (x !== null && x !== '') {
              PasswordReset(x)
            }
          }
        })
      }
    }
  }
}
</script>

<style scoped>
.login-form {
  width: 250px;
  height: 400px;
  background: white;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, calc(60px - 50%));
  padding: 50px;
}
h1 {
  text-align: center;
  -webkit-user-select: none;
}
.remember {
  margin-top: 20px;
  font-weight: bolder;
}
h4 a {
  cursor: pointer;
  text-align: center;
  color: rgb(53, 187, 187);
  text-decoration: none;
}
h4 a:nth-child(2) {
  position: absolute;
  right: 50px;
  color: rgb(138, 72, 199);
}

@media only screen and (max-width: 420px) {
  .login-form {
    margin-top: 50px;
    width: calc(100vw - 100px);
    height: 100vh;
  }
}
</style>
