<template>
  <div>
    <div id="diary">
      <div v-for="(item, index) in diary"
           :key="index"
           class="oneDiary">
        <div class="DiaryTime"><img src="../assets/icon/calendar.png"
               alt="">{{`${item.createdAt.getFullYear()}-${item.createdAt.getMonth()+1}-${item.createdAt.getDate()}`}}</div>
        <span class="goComment"
              @click="goComment(item)"></span>
        <div v-html="item.attributes.content"></div>
        <input-comment v-if="item.ifShow"
                       :blogId="item.id"></input-comment>
      </div>
    </div>
  </div>
</template>

<script>
import { queryDiary } from '../api/diary'
import inputComment from '../components/commonComment/inputComment.vue'
export default {
  components: { inputComment },
  data () {
    return {
      diary: null,
      showComment: false
    }
  },
  methods: {
    goComment (item) {
      this.$set(item, 'ifShow', !item.ifShow)
    }
  },
  created () {
    const query = queryDiary()
    query.equalTo('email', new Map(JSON.parse(window.sessionStorage.getItem('etc'))).get('email'))
    query.descending('createdAt')
    query.find().then(res => {
      for (let i = 0; i < res.length; i++) {
        res[i].ifShow = false
      }
      this.diary = res
    })
  }
}
</script>

<style scoped>
#diary {
  width: 800px;
  margin: 20px auto;
}
.oneDiary {
  background: white;
  padding: 20px;
  position: relative;
  margin: 10px auto;
}
.oneDiary >>> p img {
  max-width: 35%;
}
.goComment {
  width: 30px;
  height: 30px;
  background-image: url("../assets/icon/comment.png");
  background-size: cover;
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
}
.DiaryTime {
  font-size: 25px;
}
.DiaryTime img {
  height: 30px;
  margin-right: 20px;
}

@media only screen and (max-width: 800px) {
  #diary {
    width: calc(100% - 20px);
  }
}
@media only screen and (max-width: 400px) {
  #diary {
    width: 100%;
    margin: 0 auto;
  }
  .oneDiary {
    margin: 0 auto;
    border-bottom: gray dashed 2px;
  }
}
</style>
