<template>
  <div @click.stop="">
    <div id="inputComment"
         :class="hearerId !== '' ? 'inputCommentOne' : 'inputCommentTwo'">
      <mavon-editor :toolbars="toolbars"
                    ref=md
                    :ishljs="true"
                    :autofocus="false"
                    :placeholder="PromptMessage"
                    v-model="editorContent"
                    :codeStyle="'vs2015'"
                    @imgAdd="$imgAdd"
                    @imgDel="$imgDel"
                    style="box-shadow: none;min-height:200px;max-height:400px;border:gray solid 2px;z-index:1" />
      <span class="goTalk"
            @click="gotalk">评论</span>
    </div>
    <fater-comment-list :faterComment="item"
                        v-for="(item, index) in comment"
                        :key="index"
                        @hearer="res => hearerId = res"></fater-comment-list>
    <common-hint :hint="hint"
                 :show="showHint"></common-hint>
  </div>
</template>

<script>
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import { createFile, delFile } from '../../api/_file'
import { createComment, queryComment } from '../../api/comment'
import CommonHint from '../commonHints/commonHint.vue'
import FaterCommentList from './faterCommentList.vue'
export default {
  components: { mavonEditor, CommonHint, FaterCommentList },
  props: ['blogId'],
  data () {
    return {
      editorContent: '',
      PromptMessage: '留个免费的评论再走吧，求求你了QAQ',
      hearerId: '',
      hint: null,
      comment: null,
      showHint: 0,
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: false, // 标题
        underline: false, // 下划线
        strikethrough: false, // 中划线
        mark: false, // 标记
        superscript: false, // 上角标
        subscript: false, // 下角标
        quote: false, // 引用
        ol: false, // 有序列表
        ul: false, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: false, // 表格
        fullscreen: false, // 全屏编辑
        readmodel: false, // 沉浸式阅读
        htmlcode: false, // 展示html源码
        help: false, // 帮助
        /* 1.3.5 */
        undo: false, // 上一步
        redo: false, // 下一步
        trash: false, // 清空
        save: false, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: false, // 导航目录
        /* 2.1.8 */
        alignleft: false, // 左对齐
        aligncenter: false, // 居中
        alignright: false, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: false // 预览
      }
    }
  },
  methods: {
    gotalk () {
      if (this.$refs.md.d_value.replace().length !== 0) {
        let talker = new Map(JSON.parse(window.sessionStorage.getItem('userinfo'))).get('username')
        talker = talker !== undefined ? talker : '匿名用户'
        createComment(talker, this.$refs.md.d_render, this.hearerId, this.blogId).then(res => {
          this.hint = '评论成功'
          this.showHint++
          this.editorContent = ''
          this.resetSetItem('hearerId', '')
          this.commentList()
        }, error => {
          if (error) {
            this.hint = '评论失败'
            this.showHint++
          }
        })
      } else {
      }
    },
    $imgAdd (pos, $file) {
      const data = { base64: $file.miniurl.split(',')[1].replace('-', '+').replace('_', '/') }
      const file = createFile($file.name, data)
      file.save().then(file => {
        this.$refs.md.$img2Url(pos, file.attributes.url)
      })
    },
    $imgDel (pos) {
      delFile(pos[0].replace('http://lc-uzUuUouk.cn-n1.lcfile.com/', ''))
    },
    commentTreating (temp) {
      const arr = []
      for (var i = 0; i < this.comment.length; i++) {
        if (this.comment[i].attributes.hearerId === temp) {
          arr.push(this.comment[i])
          this.comment[i].attributes.child = this.commentTreating(this.comment[i].id)
        }
      }
      return arr
    },
    commentList () {
      const query = queryComment()
      query.equalTo('blogId', this.blogId)
      query.descending('createdAt')
      query.find().then(res => {
        this.comment = res
        this.comment = this.commentTreating('')
        this.resetSetItem('hearerId', '')
        this.resetSetItem('hearerName', '')
      })
    }
  },
  watch: {
    blogId () {
      this.commentList()
    }
  },
  created () {
    window.addEventListener('setItem', () => {
      this.hearerId = window.sessionStorage.getItem('hearerId')
      const condition = window.sessionStorage.getItem('hearerName') === '' || window.sessionStorage.getItem('hearerName') === null
      this.PromptMessage = condition ? '留个免费的评论再走吧，求求你了QAQ' : `@${window.sessionStorage.getItem('hearerName')}`
    })
    this.commentList()
  }
}
</script>

<style scoped>
.inputCommentOne {
  height: 245px;
  position: sticky;
  top: 80px;
}
.inputCommentTwo {
  height: 245px;
  position: static;
}
#inputComment .goTalk {
  float: right;
  background: tomato;
  padding: 10px 50px;
  margin: 15px 0;
  font-weight: bolder;
  cursor: pointer;
  border-radius: 5px;
}
#inputComment .goTalk:hover {
  background: orange;
}
</style>
