import AV from '@/api/AV'

export function login (email, password) {
  return AV.User.loginWithEmail(email, password)
}

export async function register (name, password, email) {
  const userinfo = new AV.Object('userinfo')
  userinfo.set('username', name)
  userinfo.set('email', email)
  const userInformation = await userinfo.save()
  const user = new AV.User()
  user.setUsername(name)
  user.setPassword(password)
  user.setEmail(email)
  user.set('userinfoId', userInformation.id)
  return user.signUp()
}
export function emailVerify (email) {
  return AV.User.requestEmailVerify(email)
}

export function PasswordReset (x) {
  AV.User.requestPasswordReset(x)
}

export function become () {
  return AV.User.become(window.sessionStorage.getItem('token'))
}
