<template>
  <div class="faterComment">
    <h4 @click.stop="goTalk">{{faterComment.attributes.talker}}</h4>
    <p>#{{`${faterComment.createdAt.getFullYear()}-${faterComment.createdAt.getMonth()+1}-${faterComment.createdAt.getDate()}`}}</p>
    <div v-html="faterComment.attributes.content"></div>
    <div class="sonComment"
         v-for="(item, index) in faterComment.attributes.child"
         :key="index">
      <son-comment-list :sonComment="item" :hearerName="faterComment.attributes.talker"></son-comment-list>
    </div>
    <hr>
  </div>
</template>

<script>
import sonCommentList from './sonCommentList.vue'
export default {
  components: { sonCommentList },
  props: ['faterComment'],
  methods: {
    goTalk () {
      this.resetSetItem('hearerId', this.faterComment.id)
      this.resetSetItem('hearerName', this.faterComment.attributes.talker)
    }
  }
}
</script>

<style scoped>
.faterComment p {
  margin: 0;
  color: rgb(75, 74, 74);
}
.faterComment h4 {
  color: tomato;
  margin: 0;
  margin-top: 5px;
  cursor: pointer;
}
.faterComment h4:hover {
  color: red;
}
.sonComment {
  margin-left: 50px;
}
</style>
