<template>
  <div style="position: relative;"
       @click.stop="clearHearer">
    <div id="archive">
      <div class="catalog">
        <one-archive :year="item"
                     v-for="(item, index) in allBlog"
                     :key="index"
                     @Submit="goBlog"
                     :blogId="blogId"></one-archive>
      </div>
      <mobile-archive-nav :allBlog="allBlog"
                          :showArchiveNav="showArchiveNav"
                          @Submit="goBlog"
                          :blogId="blogId"></mobile-archive-nav>
      <div class="openArchiveNav"
           @click="showArchiveNav = !showArchiveNav">{{showArchiveNav ? '&lt;&lt;' : '>>'}}</div>
      <div class="archiveBlog">
        <h2>{{title}}</h2>
        <div v-html="blog"></div>
        <input-comment :blogId="blogId"></input-comment>
      </div>
    </div>
  </div>
</template>

<script>
import oneArchive from '../components/Archive/oneArchive.vue'
import { queryBlog } from '../api/blog'
import InputComment from '../components/commonComment/inputComment.vue'
import MobileArchiveNav from '../components/mobile/mobileArchiveNav.vue'
export default {
  components: { oneArchive, InputComment, MobileArchiveNav },
  data () {
    return {
      allBlog: null,
      title: null,
      blog: null,
      blogId: null,
      showArchiveNav: true
    }
  },
  methods: {
    dataTreating (value) {
      const a = new Map()
      for (const year of value) {
        if (!a.has(year.createdAt.getFullYear())) {
          a.set(year.createdAt.getFullYear(), new Map())
          if (!a.get(year.createdAt.getFullYear()).has(year.createdAt.getMonth() + 1)) {
            a.get(year.createdAt.getFullYear()).set(year.createdAt.getMonth() + 1, [])
            a.get(year.createdAt.getFullYear()).get(year.createdAt.getMonth() + 1).push(year)
          } else {
            a.get(year.createdAt.getFullYear()).get(year.createdAt.getMonth() + 1).push(year)
          }
        } else {
          if (!a.get(year.createdAt.getFullYear()).has(year.createdAt.getMonth() + 1)) {
            a.get(year.createdAt.getFullYear()).set(year.createdAt.getMonth() + 1, [])
            a.get(year.createdAt.getFullYear()).get(year.createdAt.getMonth() + 1).push(year)
          } else {
            a.get(year.createdAt.getFullYear()).get(year.createdAt.getMonth() + 1).push(year)
          }
        }
      }
      return a
    },
    goBlog (value) {
      this.title = value.attributes.title
      this.blog = value.attributes.render
      this.blogId = value.id
      window.scroll(0, 0)
      this.showArchiveNav = false
    },
    clearHearer () {
      this.resetSetItem('hearerId', '')
      this.resetSetItem('hearerName', '')
    }
  },
  created () {
    const query = queryBlog()
    query.equalTo('ifFinish', true)
    query.equalTo('email', new Map(JSON.parse(window.sessionStorage.getItem('etc'))).get('email'))
    query.descending('createdAt')
    query.find().then(res => {
      this.allBlog = this.dataTreating(res)
      this.blog = res[0].attributes.render
      this.title = res[0].attributes.title
      this.blogId = res[0].id
    })
  }
}
</script>

<style scoped>
#archive {
  width: 1200px;
  display: block;
  margin: 0 auto;
  padding: 20px 0 0;
}
.catalog {
  width: 400px;
  height: calc(100vh - 130px);
  overflow: scroll;
  position: sticky;
  top: 105px;
  float: left;
}
#mobileArchiveNav {
  display: none;
}
.openArchiveNav {
  display: none;
  font-weight: bolder;
  font-size: 1.5rem;
  opacity: 0.9;
  color: aqua;
}
.archiveBlog {
  width: calc(100% - 440px);
  padding: 20px;
  display: inline-block;
  background: rgb(243, 242, 242);
  right: 0;
}
.archiveBlog >>> p img {
  max-width: 100%;
}

@media only screen and (max-width: 1200px) {
  #archive {
    width: 100%;
  }
  .catalog {
    display: none;
  }
  .archiveBlog {
    display: block;
    margin: auto;
    width: 760px;
  }
}
@media only screen and (max-width: 800px) {
  .archiveBlog {
    width: calc(100% - 40px);
  }
}
@media only screen and (max-width: 420px) {
  #archive {
    padding: 0;
  }
  #mobileArchiveNav {
    display: block;
  }
  .openArchiveNav {
    position: fixed;
    display: block;
    top: 100px;
    right: 20px;
    font-weight: bolder;
    color: aqua;
    background: rgb(138, 136, 136);
    padding: 8px;
    border-radius: 5px;
    z-index: 2;
  }
}
</style>
