import AV from '@/api/AV'

export function createFile (fileName, data) {
  return new AV.File(fileName, data)
}

export async function delFile (key) {
  const query = new AV.Query('_File')
  query.equalTo('key', key)
  const fileInformation = await query.first()
  const file = AV.File.createWithoutData(fileInformation.id)
  file.destroy()
}
