<template>
  <div id="sonarchiveItem" @click="confirm">
    <h4 :class="title.id === blogId ? 'ok' : 'no'">---{{title.createdAt.getDate()}}日-{{title.attributes.title}}</h4>
  </div>
</template>

<script>
export default {
  props: ['title', 'blogId'],
  data () {
    return {
      id: 1
    }
  },
  methods: {
    confirm () {
      this.$emit('Submit', this.title)
    }
  }
}
</script>

<style scoped>
#sonarchiveItem {
  margin-left: 50px;
  box-shadow: -5px 0px 0 aqua;
  padding: 5px 0;
  cursor: pointer;
}
h4{
  margin: 5px 20px;
  text-indent: -20px;
}
h4:hover {
  color: orange;
}
.ok {
  color: orange;
}
.no {
  color: white;
}
</style>
