import AV from '@/api/AV'

export async function officeBlog (blogInformation, render, value) {
  const become = await AV.User.become(window.sessionStorage.getItem('token'))
  const SetOneBlog = AV.Object.extend('Blog')
  const blog = window.sessionStorage.getItem('officeBlogId') ? AV.Object.createWithoutData('Blog', window.sessionStorage.getItem('officeBlogId')) : new SetOneBlog()
  blog.set('title', blogInformation.title)
  blog.set('tags', blogInformation.tag)
  blog.set('introduction', blogInformation.introduction)
  blog.set('email', become.attributes.email)
  blog.set('render', render)
  blog.set('value', value)
  return blog.save()
}

export async function queryBlogByTag (officeBlogId) {
  const query = new AV.Query('Blog')
  query.equalTo('objectId', officeBlogId)
  return await query.first()
}

export function queryBlog () {
  return new AV.Query('Blog')
}

export function setBlog (blogId) {
  return AV.Object.createWithoutData('Blog', blogId)
}
