<template>
  <div id="personCenter">
    <person-center-nav :openPersonCentorNav="openPersonCentorNav"></person-center-nav>
    <div class="personCenterPage">
      <router-view v-if="isRouterAlive"></router-view>
    </div>
    <div class="openPersonCentorNav" @click="openPersonCentorNav = !openPersonCentorNav">{{openPersonCentorNav ? '&lt;&lt;' : '>>'}}</div>
  </div>
</template>

<script>
import personCenterNav from '../components/personCenter/personCenterNav.vue'
export default {
  components: { personCenterNav },
  provide () {
    return {
      reload: this.reload
    }
  },
  data () {
    return {
      isRouterAlive: true,
      openPersonCentorNav: true
    }
  },
  methods: {
    reload () {
      this.isRouterAlive = false
      this.$nextTick(function () {
        this.isRouterAlive = true
      })
    }
  },
  beforeDestroy () {
    this.$emit('leavePersonCenter', true)
  }
}
</script>

<style scoped>
#personCenter {
  min-height: 100vh;
  width: 100%;
}
.personCenterPage {
  width: calc(100% - 300px);
  padding: 25px;
  min-height: calc(100vh - 50px);
  background: rgb(224, 223, 223);
  position: absolute;
  right: 0;
}
.openPersonCentorNav {
  display: none;
}
@media only screen and (max-width: 420px){
  .personCenterPage {
    width: calc(100vw - 50px);
    position: relative;
    overflow: scroll;
  }
  .openPersonCentorNav {
    position: fixed;
    display: block;
    top: 100px;
    right: 20px;
    font-weight: bolder;
    color: aqua;
    background: rgb(138, 136, 136);
    padding: 10px;
    border-radius: 5px;
    z-index: 2;
  }
}
</style>
