<template>
  <div id="textInput">
    <input :type="type"
           required
           v-model="content"
           @change="handlerulg">
    <label>{{name}}</label>
    <common-hint :hint="hint"
                 :show="ifShow"></common-hint>
  </div>
</template>

<script>
import commonHint from '../commonHints/commonHint'
export default {
  props: ['type', 'name', 'rule', 'hint', 'createContent'],
  components: {
    commonHint
  },
  data () {
    return {
      content: '',
      oldContent: '',
      ifShow: 0
    }
  },
  methods: {
    handlerulg () {
      const rue = new RegExp(this.rule)
      if (rue.test(this.content) || this.content === '') {
        this.$emit('inputChange', this.content)
        this.oldContent = this.content
      } else {
        this.ifShow++
        this.content = this.oldContent
      }
    }
  },
  watch: {
    createContent () {
      this.content = this.createContent
      this.oldContent = this.createContent
    }
  }
}
</script>

<style scoped>
#textInput {
  position: relative;
}
#textInput input {
  border: none;
  border-bottom: black solid 2px;
  width: 100%;
  height: 40px;
  outline: none;
  background: none;
  font-size: 16px;
}
#textInput label {
  transition: all 0.5s;
  font-weight: bolder;
  font-size: 1rem;
  color: rgb(122, 120, 120);
  position: absolute;
  left: 0;
  top: 1rem;
}
#textInput input:focus ~ label,
#textInput input:valid ~ label {
  transform: translateY(-35px);
  opacity: 0;
}
</style>
