<template>
  <div id="register">
    <common-hint :hint="hint"
                 :show='ifShow'></common-hint>
    <div class="register-form">
      <h1>Register</h1>
      <form>
        <text-input type='text'
                    name='Email'
                    hint='邮箱输入格式错误'
                    rule='^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$'
                    @inputChange="res => Email = res"></text-input>
        <text-input type='text'
                    name='Name'
                    hint='昵称只能为3-16个字组成'
                    style="margin-bottom:20px"
                    rule='^.{3,16}$'
                    @inputChange="res => Name = res"></text-input>
        <text-input type='Password'
                    name='Password'
                    hint='密码需为6-16位'
                    rule='^.{6,16}$'
                    @inputChange="res => Password = res"></text-input>
        <text-input type='Password'
                    name='Confirm Password'
                    rule='^.{6,16}$'
                    @inputChange="res => Confirm_Password = res"></text-input>
      </form>
        <booth-btn name='SIGN UP'
                   @Submit="goRegister"></booth-btn>
      <h4>
        <router-link to="/home">Go Home</router-link>
        <router-link to="/login">Go Login</router-link>
      </h4>
    </div>
  </div>
</template>

<script>
import boothBtn from '../components/commonBtn/boothBtn.vue'
import TextInput from '../components/commonInput/textInput.vue'
import CommonHint from '../components/commonHints/commonHint'
import { register, emailVerify } from '../api/_user.js'
export default {
  data () {
    return {
      Email: '',
      Name: '',
      Password: '',
      Confirm_Password: '',
      ifShow: 0,
      hint: null
    }
  },
  components: {
    boothBtn,
    TextInput,
    CommonHint
  },
  methods: {
    goRegister () {
      if (this.Name !== '' && this.Password !== '') {
        if (this.Password === this.Confirm_Password) {
          register(this.Name, this.Password, this.Email).then(res => {
            emailVerify(this.Email)
            this.hint = '注册成功,验证信息已经发送至您的QQ邮箱，请进行验证'
            this.ifShow++
          },
          (error) => {
            if (error) {
              this.ifShow++
              this.hint = error
            }
          })
        } else {
          this.hint = '两次密码输入不统一，请重新输入'
          this.ifShow++
        }
      } else {
        this.hint = '请将信息填写完整'
        this.ifShow++
      }
    }
  }
}
</script>

<style scoped>
.register-form {
  width: 250px;
  height: 400px;
  background: white;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, calc(60px - 50%));
  padding: 50px;
}
h1 {
  text-align: center;
  -webkit-user-select: none;
}
h4 a {
  cursor: pointer;
  color: rgb(53, 187, 187);
  text-decoration: none;
}
h4 a:nth-child(2) {
  position: absolute;
  right: 50px;
  color: rgb(138, 72, 199);
}
@media only screen and (max-width: 420px) {
  .register-form {
    margin-top: 50px;
    width: calc(100vw - 100px);
    height: 100vh;
  }
}
</style>
