<template>
  <div class="popContainer" @click.stop="$emit('Submit')">
    <div id="etcInformation" @click.stop="">
      <div class="nowEtc">
        <img :src="etcPhoto"
             alt="">
        <ul>
          <li>昵称： {{etcUsername}}</li>
          <li>性别： {{etcSex}}</li>
          <li>邮箱： {{etcEmail}}</li>
          <li>注册时间： {{`${etcCreateTime.getFullYear()}-${etcCreateTime.getMonth()+1}-${etcCreateTime.getDate()}`}}</li>
          <li>爱好： {{etcHobby}}</li>
          <li>座右铭： {{etcMotto}}</li>
          <li>最想说的话： {{etcSay}}</li>
        </ul>
      </div>
      <div class="otherEtc">
        <h3 style="margin: 0">Order User [{{otherEtcCount}}]</h3>
        <div v-for="(item, index) in allUser"
             :key="index"
             class="oneEtc"
             @click="goEtc(item.attributes.email)">
          <img :src="item.attributes.photo === undefined ? require('../../assets/photo.jpeg') : item.attributes.photo"
               alt="">
          <div class="oneEtcInformation">
            <p>{{item.attributes.username}}</p>
            <p>{{item.attributes.sex}}</p>
          </div>
          <hr style="margin: 0">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { queryAllUserinfo, queryToEmail } from '../../api/userinfo'
export default {
  data () {
    return {
      etcPhoto: require('../../assets/photo.jpeg'),
      etcUsername: null,
      etcSex: null,
      etcEmail: null,
      etcCreateTime: null,
      etcHobby: null,
      etcMotto: null,
      etcSay: null,
      allUser: null,
      otherEtcCount: 0,
      ifShow: this.showEtcList
    }
  },
  methods: {
    goEtc (email) {
      queryToEmail(email).then(res => {
        window.sessionStorage.setItem('etc', JSON.stringify([...res]))
        this.$emit('Submit')
        this.$emit('refreshHome')
      })
    }
  },
  created () {
    const etc = new Map(JSON.parse(window.sessionStorage.getItem('etc')))
    this.etcPhoto = etc.get('photo') === '' || etc.get('photo') === undefined || etc.get('photo') === null ? this.etcPhoto : etc.get('photo')
    this.etcUsername = etc.get('username')
    this.etcSex = etc.get('sex')
    this.etcEmail = etc.get('email')
    this.etcCreateTime = new Date(etc.get('createTime'))
    this.etcHobby = etc.get('hobby')
    this.etcMotto = etc.get('motto')
    this.etcSay = etc.get('say')

    const query = queryAllUserinfo()
    query.notEqualTo('email', this.etcEmail)
    query.find().then(res => {
      this.allUser = res
    })
    query.count().then(res => {
      this.otherEtcCount = res
    })
  }
}
</script>

<style scoped>
#etcInformation {
  width: 800px;
  height: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}
.nowEtc {
  width: 460px;
  height: 460px;
  padding: 20px;
  background: rgb(39, 39, 39);
  display: inline-block;
  border-right: white solid 5px;
  overflow: scroll;
}
.otherEtc {
  width: 255px;
  height: 460px;
  background: rgb(39, 39, 39);
  float: right;
  padding: 20px;
  overflow: scroll;
}

.nowEtc img {
  width: 150px;
  height: 150px;
  display: inline-block;
}

.nowEtc ul {
  padding: 0 20px;
}
.nowEtc ul li {
  margin: 10px 0;
}
.oneEtc {
  height: 50px;
  margin: 20px 0;
  cursor: pointer;
}
.oneEtc:hover {
  color: tomato;
}
.oneEtc img {
  height: 100%;
  display: inline-block;
}
.oneEtc .oneEtcInformation {
  display: inline-block;
}
.oneEtc .oneEtcInformation p {
  margin: 5px 10px;
}
</style>
