<template>
  <div id="pageNumber">
    <span @click="goPage(0)">hand</span>
    <span v-for="(item, index) in allPage"
          :key="index"
          :style="index === newPage ? style : null"
          @click="goPage(index)">{{item}}</span>
    <span @click="goPage(allPage - 1)">end</span>
  </div>
</template>

<script>
export default {
  props: ['allPage', 'newPage'],
  data () {
    return {
      style: {
        background: 'tomato',
        color: 'white'
      }
    }
  },
  methods: {
    goPage (page) {
      this.$emit('newPage', page)
    }
  }
}
</script>

<style scoped>
#pageNumber {
  width: 100%;
  display: -webkit-flex;
  flex-wrap: nowrap;
  justify-content: center;
  margin: 20px 0 100px;
}
#pageNumber span {
  margin: 20px 5px;
  font-weight: bolder;
  cursor: pointer;
  background: white;
  display: inline-block;
  padding: 5px 15px;
}
#pageNumber span:hover {
  background: tomato;
  color: white;
}
</style>
