<template>
  <div id="unFinishBlog">
    <div v-for="(item, index) in blog"
         :key="index">
      <div class="blogInformation">
        <h2 @click="goBlog(item.id)">{{item.attributes.title}}</h2>
        <span><img src="../../assets/icon/calendar.png"
               alt="">{{`${item.createdAt.getFullYear()}-${item.createdAt.getMonth()+1}-${item.createdAt.getDate()}`}}</span>
        <span><img src="../../assets/icon/tag.png"
               alt="">{{item.attributes.tags}}</span>
      </div>
      <div class="setBlog">
        <person-center-btn name='标记为未完成'
                           @Submit="unFinishBlog(item.id)"></person-center-btn>
      </div>
      <hr>
    </div>
  </div>
</template>

<script>
import { queryBlog, setBlog } from '../../api/blog'
import personCenterBtn from '../commonBtn/personCenterBtn.vue'
export default {
  components: { personCenterBtn },
  inject: ['reload'],
  data () {
    return {
      blog: null
    }
  },
  methods: {
    unFinishBlog (blogId) {
      const unfinish = setBlog(blogId)
      unfinish.set('ifFinish', false)
      unfinish.save().then(res => {
        this.reload()
      })
    },
    goBlog (blogId) {
      window.sessionStorage.setItem('blogId', blogId)
      this.$router.push('/blogContent')
    }
  },
  created () {
    const query = queryBlog()
    query.equalTo('ifFinish', true)
    query.equalTo('email', new Map(JSON.parse(window.sessionStorage.getItem('userinfo'))).get('email'))
    query.descending('createdAt')
    query.find().then(res => {
      this.blog = res
    })
  }
}
</script>

<style scoped>
#unFinishBlog {
  position: relative;
}
#unFinishBlog .blogInformation {
  width: calc(100% - 150px);
  display: inline-block;
}
#unFinishBlog .setBlog {
  width: 150px;
  float: right;
  margin-top: 25px;
}
h2:hover {
  cursor: pointer;
  color: tomato;
}
span {
  margin-right: 25px;
  font-size: 20px;
  color: rgb(92, 91, 91);
}
img {
  width: 20px;
  margin-right: 5px;
}
</style>
