<template>
  <div class="photo"
       ref="photo"
       :style="{backgroundImage:background === null ? 'url(' + require('../assets/photo.jpeg') + ')' : 'url('+ background +')'}"
       @mousedown="mouseDownHandleelse($event)"
       @mouseup="mouseUpHandleelse($event)">
  </div>
</template>

<script>
export default {
  name: 'photo',
  data () {
    return {
      moveDataelse: {
        x: null,
        y: null
      },
      beforeMoveX: null,
      beforeMoveY: null,
      background: require('../assets/photo.jpeg')
    }
  },
  created () {
    if (window.sessionStorage.getItem('userinfo')) {
      const a = new Map(JSON.parse(window.sessionStorage.getItem('userinfo'))).get('photo')
      this.background = a === '' || a === undefined || a === null ? this.background : a
    }
  },
  methods: {
    mouseDownHandleelse (event) {
      this.moveDataelse.x = event.pageX - this.$refs.photo.offsetLeft
      this.moveDataelse.y = event.pageY - this.$refs.photo.offsetTop
      event.currentTarget.style.cursor = 'move'
      window.onmousemove = this.mouseMoveHandleelse
      this.beforeMoveX = this.$refs.photo.offsetLeft
      this.beforeMoveY = this.$refs.photo.offsetTop
    },
    mouseMoveHandleelse (event) {
      const moveLeft = event.pageX - this.moveDataelse.x
      const moveTop = event.pageY - this.moveDataelse.y
      this.$refs.photo.style.left = moveLeft + 'px'
      this.$refs.photo.style.top = moveTop + 'px'
    },
    mouseUpHandleelse (event) {
      window.onmousemove = null
      event.currentTarget.style.cursor = 'move'

      if (this.beforeMoveY === this.$refs.photo.offsetTop && this.beforeMoveX === this.$refs.photo.offsetLeft) {
        if (window.sessionStorage.getItem('token')) {
          this.$emit('personCenter', false)
          this.$router.push('personCenter')
        } else {
          this.$router.push('login')
        }
      }
    }
  }
}
</script>

<style scoped>
.photo {
  height: 130px;
  width: 130px;
  position: fixed;
  border-radius: 50%;
  right: 20px;
  top: 200px;
  cursor: pointer;
  z-index: 999;
  background-size: cover;
  background-position: center;
  overflow: hidden;
  box-shadow: 0px 0px 10px white;
}
img {
  width: 100%;
  -webkit-user-select: none;
}
@media only screen and (max-width: 800px) {
  .photo {
    display: none;
  }
}
</style>
