<template>
  <div id="home">
    <div class="tag">
      <user :etcPhoto="etcPhoto"
            :etcName="etcName"
            :blogCount="blogCount"
            :diaryCount="diaryCount"
            @Submit="() => showEtcList = true"></user>
      <tag :allTag="allTag"
           @tagName="newTag"></tag>
    </div>
    <div style="float:none"></div>
    <div class="homeBlog">
      <blog v-for="(item, index) in blog"
            :key="index"
            :blogInformation="item"></blog>
      <page-number :allPage="allPage"
                   :newPage="newPage"
                   @newPage="newPageBlog"></page-number>
    </div>
    <etc-information @Submit="() => showEtcList = false"
                     v-if="showEtcList"
                     @refreshHome="() => refresh()"></etc-information>
  </div>
</template>

<script>
import Blog from '../components/Home/Blog.vue'
import PageNumber from '../components/Home/pageNumber.vue'
import Tag from '../components/Home/Tag.vue'
import User from '../components/Home/User.vue'
import { queryBlog } from '../api/blog'
import EtcInformation from '../components/Home/etcInformation.vue'
export default {
  inject: ['refresh'],
  components: {
    User,
    Tag,
    Blog,
    PageNumber,
    EtcInformation
  },
  data () {
    return {
      allTag: null,
      blog: null,
      allPage: 0,
      newPage: 0,
      etcPhoto: require('../assets/photo.jpeg'),
      etcName: null,
      blogCount: null,
      diaryCount: null,
      showEtcList: false
    }
  },
  methods: {
    newPageBlog (newPage) {
      window.scroll(0, 0)
      this.newPage = newPage
      const query = queryBlog()
      query.equalTo('ifFinish', true)
      query.equalTo('email', new Map(JSON.parse(window.sessionStorage.getItem('etc'))).get('email'))
      query.descending('comment')
      query.limit(8)
      query.skip(8 * this.newPage)
      query.find().then(res => {
        this.blog = res
      })
    },
    newTag (tagName) {
      const query = queryBlog()
      query.equalTo('ifFinish', true)
      query.equalTo('email', new Map(JSON.parse(window.sessionStorage.getItem('etc'))).get('email'))
      query.descending('comment')
      if (tagName !== 'All') {
        query.equalTo('tags', tagName)
      }
      query.limit(8)
      query.find().then(res => {
        this.blog = res
      })
      query.count().then((count) => {
        this.allPage = Math.ceil(count / 8)
      })
    }
  },
  created () {
    if (window.sessionStorage.getItem('etc')) {
      this.newTag('All')
      const etc = new Map(JSON.parse(window.sessionStorage.getItem('etc')))
      this.etcName = etc.get('username')
      this.blogCount = etc.get('blogCount')
      this.diaryCount = etc.get('diaryCount')
      this.etcPhoto = etc.get('photo') === '' || etc.get('photo') === null || etc.get('photo') === undefined ? this.etcPhoto : etc.get('photo')
      const query = queryBlog()
      query.equalTo('ifFinish', true)
      query.equalTo('email', etc.get('email'))
      query.descending('comment')
      query.find().then(res => {
        var ary = {}
        for (let i = 0; i < res.length; i++) {
          if (res[i]._serverData.tags in ary) {
            ary[res[i]._serverData.tags] = ary[res[i]._serverData.tags] + 1
          } else {
            ary[res[i]._serverData.tags] = 1
          }
        }
        this.allTag = ary
      })
    }
  }
}
</script>

<style scoped>
#home {
  width: 1200px;
  margin: 20px auto;
  position: relative;
  display: block;
}
#home .tag {
  width: 250px;
  background: white;
  padding: 50px;
  position: sticky;
  top: -385px;
  float: left;
}
#home .homeBlog {
  display: inline-block;
  width: calc(100% - 375px);
  right: 0;
  margin-left: 25px;
}

@media only screen and (max-width: 1200px) {
  #home {
    width: 100%;
  }
  #home .tag {
    display: none;
  }
  #home .homeBlog {
    display: block;
    margin: auto;
    width: 800px;
  }
}
@media only screen and (max-width: 800px) {
  #home .homeBlog {
    width: calc(100% - 10px);
  }
}
@media only screen and (max-width: 420px) {
  #home {
    margin: 0;
  }
  #home .homeBlog {
    width: 100%;
  }
}
</style>
