import AV from './AV'

export async function setUserinfo (photo, username, sex, hobby, motto, say) {
  const become = await AV.User.become(window.sessionStorage.getItem('token'))
  const userinfo = AV.Object.createWithoutData('userinfo', become.attributes.userinfoId)
  userinfo.set('photo', photo)
  userinfo.set('username', username)
  userinfo.set('sex', sex)
  userinfo.set('hobby', hobby)
  userinfo.set('motto', motto)
  userinfo.set('say', say)
  return userinfo.save()
}

export async function queryUserinfo () {
  const become = await AV.User.become(window.sessionStorage.getItem('token'))
  const query = new AV.Query('userinfo')
  query.equalTo('objectId', become.attributes.userinfoId)
  return query.first()
}

export async function queryToEmail (email) {
  const queryUser = new AV.Query('userinfo')
  queryUser.equalTo('email', email)
  const user = await queryUser.first()

  const queryBlog = new AV.Query('Blog')
  queryBlog.equalTo('email', email)
  queryBlog.equalTo('ifFinish', true)
  const blogCount = await queryBlog.count()

  const diary = new AV.Query('diary')
  diary.equalTo('email', email)
  const diaryCount = await diary.count()

  const userinfo = new Map()
  userinfo.set('blogCount', blogCount)
  userinfo.set('username', user.attributes.username)
  userinfo.set('email', user.attributes.email)
  userinfo.set('photo', user.attributes.photo)
  userinfo.set('sex', user.attributes.sex)
  userinfo.set('motto', user.attributes.motto)
  userinfo.set('hobby', user.attributes.hobby)
  userinfo.set('say', user.attributes.say)
  userinfo.set('createTime', user.createdAt)
  userinfo.set('diaryCount', diaryCount)
  return userinfo
}

export function queryAllUserinfo () {
  return new AV.Query('userinfo')
}
