<template>
  <div id="goTalkBtn" @click="goTalkBtn"></div>
</template>

<script>
export default {
  props: ['place'],
  methods: {
    goTalkBtn () {
      window.scrollTo(0, document.getElementById('inputComment').getBoundingClientRect().top + document.documentElement.scrollTop - document.getElementById('inputComment').offsetHeight)
    }
  }
}
</script>

<style scoped>
#goTalkBtn {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: white;
  background-image: url('../../assets/icon/comment.png');
  background-size: contain;
  cursor: pointer;
  margin: 5px auto;
}
</style>
