<template>
  <div class="oneArchive">
    <h2 @click="divShow()">---{{year[0]}}</h2>
    <div v-if="isShow">
      <son-archive :month="item"
                   v-for="(item, index) in year[1]"
                   :key="index"
                   v-on="$listeners"
                   v-bind="$attrs"></son-archive>
    </div>
  </div>
</template>

<script scoped>
import sonArchive from './sonArchive.vue'
export default {
  components: { sonArchive },
  props: ['year'],
  data () {
    return {
      isShow: false
    }
  },
  methods: {
    divShow () {
      this.isShow = !this.isShow
    }
  }
}
</script>

<style scoped>
.oneArchive {
  display: inline-block;
  box-shadow: -5px 0px 0 aqua;
  color: white;
  padding: 20px 0;
  width: calc(100% - 5px);
  margin-left: 5px;
}
h2:hover {
  color: orange;
  cursor: pointer;
}
</style>
