import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '@/views/home'
import login from '@/views/login'
import register from '@/views/register'
import archive from '@/views/archive'
import diary from '@/views/diary'
import personCenter from '@/views/personCenter'
import userinfo from '@/components/personCenter/userinfo'
import office from '@/components/personCenter/office'
import news from '@/components/personCenter/news'
import allBlog from '@/components/personCenter/allBlog'
import unfinishBlog from '@/components/personCenter/unfinishBlog'
import writeDiary from '@/components/personCenter/writeDiary'
import editUserinfo from '@/components/personCenter/editUserinfo'
import blogContent from '@/views/blogContent'
import allDiary from '@/components/personCenter/allDiary'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    component: home,
    meta: {
      index: 1
    }
  },
  {
    path: '/login',
    component: login,
    meta: {
      index: 2
    }
  },
  {
    path: '/register',
    component: register,
    meta: {
      index: 3
    }
  },
  {
    path: '/archive',
    component: archive,
    meta: {
      index: 4
    }
  },
  {
    path: '/diary',
    component: diary,
    meta: {
      index: 5
    }
  },
  {
    path: '/blogContent',
    component: blogContent,
    meta: {
      index: 6
    }
  },
  {
    path: '/personCenter',
    component: personCenter,
    meta: {
      index: 7
    },
    children: [
      {
        path: '',
        redirect: 'userinfo'
      },
      {
        path: 'userinfo',
        component: userinfo
      },
      {
        path: 'office',
        component: office
      },
      {
        path: 'news',
        component: news
      },
      {
        path: 'allBlog',
        component: allBlog
      },
      {
        path: 'unfinishBlog',
        component: unfinishBlog
      },
      {
        path: 'writeDiary',
        component: writeDiary
      },
      {
        path: 'editUserinfo',
        component: editUserinfo
      },
      {
        path: 'allDiary',
        component: allDiary
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  if (to.path === '/personCenter/userinfo' && !window.sessionStorage.getItem('token')) {
    next('/home')
  } else {
    next()
  }
})

router.afterEach((to, from, next) => {
  window.scrollTo(0, 0)
})
export default router
