<template>
  <div id="allDiary">
    <div v-for="(item, index) in diary"
         :key="index"
         class="oneDiary">
      <div class="DiaryTime"><img src="../../assets/icon/calendar.png"
             alt="">{{`${item.createdAt.getFullYear()}-${item.createdAt.getMonth()+1}-${item.createdAt.getDate()}`}}</div>
      <div v-html="item.attributes.content"></div>
      <person-center-btn name='删除'
                         @Submit="delDiary(item.id)"></person-center-btn>
      <hr>
    </div>
  </div>
</template>

<script>
import { queryDiary, setDiary } from '../../api/diary'
import personCenterBtn from '../commonBtn/personCenterBtn.vue'
export default {
  inject: ['reload'],
  components: { personCenterBtn },
  data () {
    return {
      diary: null
    }
  },
  methods: {
    delDiary (diaryId) {
      const r = confirm('您确定删除这篇日记吗？')
      if (r) {
        const diary = setDiary(diaryId)
        diary.destroy().then(res => {
          this.reload()
        })
      }
    }
  },
  created () {
    const query = queryDiary()
    query.equalTo('email', new Map(JSON.parse(window.sessionStorage.getItem('userinfo'))).get('email'))
    query.find().then(res => {
      this.diary = res
    })
  }
}
</script>

<style scoped>
.DiaryTime img {
  height: 20px;
  width: 20px;
  margin-right: 20px;
}
.oneDiary div >>> p img {
  max-width: 30%;
}
</style>
