<template>
  <div id="editUserinfo">
    <img :src="photo"
         alt=""
         @click="isShow = true">
    <text-input name='昵称'
                hint='昵称只能由3-16个字符组成'
                rule='^.{3,16}$'
                :createContent="username"
                @inputChange="res => username = res"
                type='text'></text-input>
    <input id="man"
           type="radio"
           v-model="sex"
           value="男"
           name="1" />男
    <input id="woman"
           type="radio"
           v-model="sex"
           value="女"
           name="1" />女
    <text-input name='爱好'
                hint='请输入3-20个字表达你的爱好'
                rule='^.{3,20}$'
                :createContent="hobby"
                @inputChange="res => hobby = res"
                type='text'></text-input>
    <text-input name='座右铭'
                hint='请将座右铭控制在3-50字之间'
                rule='^.{3,50}$'
                :createContent="motto"
                @inputChange="res => motto = res"
                type='text'></text-input>
    <textarea-input name='最想说的话'
                    hint='请将字数控制在3-600字之间'
                    rule='^.{3,600}$'
                    :createContent="say"
                    @inputChange="res => say = res"></textarea-input>
    <person-center-btn name='保存编辑'
                       @Submit="Userinfo"></person-center-btn>
    <cropper-image @photoImage="res => photo = res"
                   @show="res => isShow = res"
                   v-if="isShow"></cropper-image>
    <common-hint :hint="hint"
                 :show="showHint"></common-hint>
  </div>
</template>

<script>
import TextareaInput from '../commonInput/textareaInput.vue'
import textInput from '../commonInput/textInput.vue'
import CropperImage from '../cropperImage.vue'
import { setUserinfo, queryUserinfo, queryToEmail } from '../../api/userinfo'
import CommonHint from '../commonHints/commonHint.vue'
import PersonCenterBtn from '../commonBtn/personCenterBtn.vue'
export default {

  components: { textInput, TextareaInput, CropperImage, CommonHint, PersonCenterBtn },
  data () {
    return {
      photo: require('../../assets/photo.jpeg'),
      username: null,
      sex: null,
      hobby: null,
      motto: null,
      say: null,
      isShow: false,
      hint: '错误',
      showHint: 0
    }
  },
  watch: {
    photo () {
      this.isShow = false
    }
  },
  methods: {
    Userinfo () {
      setUserinfo(this.photo, this.username, this.sex, this.hobby, this.motto, this.say).then(res => {
        console.log(res)
        queryToEmail(new Map(JSON.parse(window.sessionStorage.getItem('userinfo'))).get('email')).then(res => {
          window.sessionStorage.setItem('userinfo', JSON.stringify([...res]))
          this.$router.push('userinfo')
          this.$router.go(0)
        })
      }, (error) => {
        if (error) {
          this.hint = '个人信息修改失败，请重新登录'
          this.showHint++
        }
      })
    }
  },
  created () {
    queryUserinfo().then(res => {
      this.photo = res.attributes.photo === '' || res.attributes.photo === undefined || res.attributes.photo === null ? this.photo : res.attributes.photo
      this.username = res.attributes.username
      this.sex = res.attributes.sex
      this.hobby = res.attributes.hobby
      this.motto = res.attributes.motto
      this.say = res.attributes.say
    })
  }
}
</script>

<style scoped>
#editUserinfo {
  padding: 0;
  position: relative;
}
#editUserinfo img {
  width: 240px;
  height: 240px;
  display: inline-block;
  cursor: pointer;
}
input {
  margin: 25px 15px 25px 5px;
  cursor: pointer;
}
</style>
