<template>
  <div id="blogContent"
       @click.stop="clearHearer">
    <div class="content">
      <h1>{{title}}</h1>
      <div v-html="blog"></div>
      <input-comment :blogId="blogId"></input-comment>
    </div>
    <div class="doSomething">
      <go-top></go-top>
      <go-talk-btn></go-talk-btn>
      <like-btn @Submit="goLike" :ifLike="ifLike"></like-btn>
    </div>
    <common-hint :hint="hint"
                 :show="showHint"></common-hint>
  </div>
</template>

<script>
import inputComment from '../components/commonComment/inputComment.vue'
import { queryBlog, setBlog } from '../api/blog'
import { become } from '../api/_user'
import GoTop from '../components/commonBtn/goTop.vue'
import GoTalkBtn from '../components/commonBtn/goTalkBtn.vue'
import LikeBtn from '../components/commonBtn/likeBtn.vue'
import CommonHint from '../components/commonHints/commonHint.vue'
export default {
  components: { inputComment, GoTop, GoTalkBtn, LikeBtn, CommonHint },
  data () {
    return {
      title: null,
      blog: null,
      blogId: null,
      hint: null,
      showHint: 0,
      ifLike: false
    }
  },
  methods: {
    clearHearer () {
      this.resetSetItem('hearerId', '')
      this.resetSetItem('hearerName', '')
    },
    goLike () {
      if (window.sessionStorage.getItem('token')) {
        become().then(res => {
          const blog = setBlog(window.sessionStorage.getItem('blogId'))
          if (this.ifLike) {
            blog.remove('like', res.attributes.email)
          } else {
            blog.addUnique('like', res.attributes.email)
          }
          blog.save().then(res => {
            this.ifLike = !this.ifLike
          })
        })
      } else {
        this.hint = '请先进行登录操作'
        this.showHint++
      }
    }
  },
  created () {
    const query = queryBlog()
    query.equalTo('objectId', window.sessionStorage.getItem('blogId'))
    query.first().then(res => {
      this.blog = res.attributes.render
      this.title = res.attributes.title
      this.blogId = res.id
      const love = new Set(res.attributes.like)
      this.ifLike = love.has(new Map(JSON.parse(window.sessionStorage.getItem('userinfo'))).get('email'))
    })
    const blog = setBlog(window.sessionStorage.getItem('blogId'))
    blog.increment('pageView', 1)
    blog.save()
  },
  beforeDestroy () {
    this.clearHearer()
  }

}
</script>

<style scoped>
#blogContent {
  position: relative;
}
.content {
  width: 760px;
  min-height: 100vh;
  background: white;
  margin: 20px auto;
  padding: 20px;
}
.content >>> p img {
  max-width: 100%;
}
.doSomething {
  width: 60px;
  height: 220px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  position: fixed;
  z-index: 5;
  left: calc(50% - 500px);
  top: 450px;
}

@media only screen and (max-width: 800px) {
  .content {
    width: calc(100% - 40px);
  }
}
@media only screen and (max-width: 420px) {
  .content {
    margin: 0;
  }
}
</style>
