<template>
  <transition name="fade">
    <div id="mobileArchiveNav"
         v-if="showArchiveNav">
      <div class="oneYear"
           v-for="(item, index) in allBlog"
           :key="index">
        <h3 style="color:#4af8f8">{{item[0]}}</h3>
        <ul v-for="(allTitle, index) in item[1]"
            :key="index">
          <h4 style="color:#4af8f8">{{allTitle[0]}}月</h4>
          <li v-for="(oneTitle, index) in allTitle[1]"
              :key="index" @click="$emit('Submit', oneTitle)" :class="oneTitle.id === blogId ? 'ok' : 'no' "><span style="color:#4af8f8">{{oneTitle.createdAt.getDate()}}日 ： </span>{{oneTitle.attributes.title}}</li>
        </ul>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: ['allBlog', 'showArchiveNav', 'blogId']
}
</script>

<style scoped>
#mobileArchiveNav {
  position: fixed;
  top: 60px;
  left: 0;
  width: calc(100vw - 40px);
  background: rgba(80, 80, 80);
  height: calc(100vh - 61px);
  border-top: white solid 1px;
  padding: 20px 20px;
  color: white;
  overflow: scroll;
  z-index: 2;
}
.oneYear ul {
  padding-left: 20px;
  margin-bottom: 40px;
}
.oneYear ul li {
  margin-left: 40px;
  margin-bottom: 20px;
  font-weight: bolder;
}
.oneYear ul li:active {
  color: orange;
}
.ok {
  color: orange;
}
.no {
  color: white;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s linear;
}
.fade-enter,
.fade-leave-to {
  transform: translateX(-100vw);
}
.fade-enter-to,
.fade-leave {
  transform: translateX(0px);
}
</style>
