<template>
  <div class="popContainer" @click="$emit('show', false)">
    <div class="cropper-content" @click.stop="">
      <div class="cropper-box">
        <div class="cropper">
          <vue-cropper ref="cropper"
                       :img="option.img"
                       :outputSize="option.outputSize"
                       :outputType="option.outputType"
                       :info="option.info"
                       :canScale="option.canScale"
                       :autoCrop="option.autoCrop"
                       :autoCropWidth="option.autoCropWidth"
                       :autoCropHeight="option.autoCropHeight"
                       :fixed="option.fixed"
                       :fixedNumber="option.fixedNumber"
                       :full="option.full"
                       :fixedBox="option.fixedBox"
                       :canMove="option.canMove"
                       :canMoveBox="option.canMoveBox"
                       :original="option.original"
                       :centerBox="option.centerBox"
                       :height="option.height"
                       :infoTrue="option.infoTrue"
                       :maxImgSize="option.maxImgSize"
                       :enlarge="option.enlarge"
                       :mode="option.mode"
                       @realTime="realTime"
                       @imgLoad="imgLoad">
          </vue-cropper>
        </div>
      </div>
      <!--预览效果图-->
      <div class="show-preview">
        <div class="show-preview-one">
          <div :style="previews.div"
               class="preview">
            <img :src="previews.url"
                 :style="previews.img">
          </div>
        </div>
        <div class="show-preview-two">
          <div :style="previews.div"
               class="preview">
            <img :src="previews.url"
                 :style="previews.img">
          </div>
        </div>
      </div>
      <!--底部操作工具按钮-->
      <div class="footer-btn">
        <label class="btn"
               for="uploads">选择图片</label>
        <input type="file"
               id="uploads"
               style="position:absolute; clip:rect(0 0 0 0);"
               accept="image/png, image/jpeg, image/gif, image/jpg"
               @change="selectImg($event)">
        <span @click="changeScale(1)">放大</span>
        <span @click="changeScale(-1)">缩小</span>
        <span @click="rotateLeft">↺ 左旋转</span>
        <span @click="rotateRight">↻ 右旋转</span>
        <span @click="uploadImg('base64')">上传图片</span>
      </div>
    </div>
  </div>
</template>

<script>
import { VueCropper } from 'vue-cropper'
export default {
  name: 'CropperImage',
  components: {
    VueCropper
  },
  props: ['Name'],
  data () {
    return {
      name: this.Name,
      previews: {},
      option: {
        img: '', // 裁剪图片的地址
        outputSize: 1, // 裁剪生成图片的质量(可选0.1 - 1)
        outputType: 'jpeg', // 裁剪生成图片的格式（jpeg || png || webp）
        info: true, // 图片大小信息
        canScale: true, // 图片是否允许滚轮缩放
        autoCrop: true, // 是否默认生成截图框
        autoCropWidth: 175, // 默认生成截图框宽度
        autoCropHeight: 175, // 默认生成截图框高度
        fixed: true, // 是否开启截图框宽高固定比例
        fixedNumber: [1, 1], // 截图框的宽高比例
        full: false, // false按原比例裁切图片，不失真
        fixedBox: true, // 固定截图框大小，不允许改变
        canMove: false, // 上传图片是否可以移动
        canMoveBox: true, // 截图框能否拖动
        original: false, // 上传图片按照原始比例渲染
        centerBox: false, // 截图框是否被限制在图片里面
        height: true, // 是否按照设备的dpr 输出等比例图片
        infoTrue: false, // true为展示真实输出图片宽高，false展示看到的截图框宽高
        maxImgSize: 3000, // 限制图片最大宽度和高度
        enlarge: 2, // 图片根据截图框输出比例倍数
        mode: 'contain 100% auto' // 图片默认渲染方式
      }
    }
  },
  methods: {
    // 初始化函数
    imgLoad (msg) {
      console.log('工具初始化函数=====' + msg)
    },
    // 图片缩放
    changeScale (num) {
      num = num || 1
      this.$refs.cropper.changeScale(num)
    },
    // 向左旋转
    rotateLeft () {
      this.$refs.cropper.rotateLeft()
    },
    // 向右旋转
    rotateRight () {
      this.$refs.cropper.rotateRight()
    },
    // 实时预览函数
    realTime (data) {
      this.previews = data
    },
    // 选择图片
    selectImg (e) {
      const file = e.target.files[0]
      if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(e.target.value)) {
        this.$message({
          message: '图片类型要求：jpeg、jpg、png',
          type: 'error'
        })
        return false
      }
      // 转化为blob
      const reader = new FileReader()
      reader.onload = (e) => {
        let data
        if (typeof e.target.result === 'object') {
          data = window.URL.createObjectURL(new Blob([e.target.result]))
        } else {
          data = e.target.result
        }
        this.option.img = data
      }
      // 转化为base64
      reader.readAsDataURL(file)
    },
    // 上传图片
    uploadImg (type) {
      if (type === 'base64') {
        this.$refs.cropper.getCropData(async (data) => {
          this.$emit('photoImage', data)
        })
      }
    }
  }
}
</script>

<style scoped>
.cropper-content {
  position: fixed;
  width: 600px;
  height: 450px;
  left: calc(50%);
  top: 50%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 25px;
}
.cropper-box {
  width: 400px;
  display: inline-block;
}
.cropper {
  width: auto;
  height: 400px;
}
.show-preview {
  display: inline-block;
  width: 175px;
  height: 380px;
  position: absolute;
  right: 25px;
  background: rgb(143, 141, 141);
  padding: 10px;
}
.preview {
  overflow: hidden;
  display: inline-block;
}
.show-preview-one {
  width: 175px;
  height: 175px;
  margin-bottom: 25px;
}
.show-preview-two {
  width: 175px;
  height: 175px;
  border-radius: 50%;
  overflow: hidden;
}
.footer-btn {
  height: 70px;
  display: -webkit-flex;
  flex-wrap: nowrap;
  justify-content: center;
}
.footer-btn span,
.btn {
  line-height: 50px;
  background: tomato;
  margin: 10px 7px;
  padding: 0 10px;
  cursor: pointer;
  font-weight: bolder;
  color: white;
  border-radius: 5px;
}
.footer-btn span:hover,
.btn:hover {
  background: #4af8f8;
  color: black;
}
.upload-btn {
  flex: 1;
  -webkit-flex: 1;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
}
</style>
