<template>
  <div id="textareaInput">
    <textarea required
              v-model="content"
              @change="handlerulg"
              maxlength=600></textarea>
    <label>{{name}}</label>
    <common-hint :hint="hint"
                 :show="ifShow"></common-hint>
  </div>
</template>

<script>
import commonHint from '../commonHints/commonHint.vue'
export default {
  components: { commonHint },
  props: ['name', 'rule', 'hint', 'createContent'],
  data () {
    return {
      content: '',
      oldContent: '',
      ifShow: 0
    }
  },
  methods: {
    handlerulg () {
      const rue = new RegExp(this.rule)
      if (rue.test(this.content) || this.content === '') {
        this.$emit('inputChange', this.content)
        this.oldContent = this.content
      } else {
        this.ifShow++
        this.content = this.oldContent
      }
    }
  },
  watch: {
    createContent () {
      this.content = this.createContent
      this.oldContent = this.createContent
    }
  }
}
</script>

<style scoped>
#textareaInput {
  position: relative;
  width: calc(100% - 40px);
}
textarea {
  height: 200px;
  width: 100%;
  border: none;
  border: black solid 2px;
  margin-top: 40px;
  background: none;
  outline: none;
  font-size: 20px;
  padding: 20px;
}
label {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: bolder;
  font-size: 1rem;
  color: rgb(122, 120, 120);
  transition: all 0.5s;
  user-select: none;
}
#textareaInput textarea:focus ~ label,
#textareaInput textarea:valid ~ label {
  top: 40px;
  opacity: 0;
}
</style>
