<template>
  <div id="userinfo">
    <div class="vistingCard">
      <img :src="photo"
           alt="">
      <ul>
        <li>用户名：{{username}}</li>
        <li>性别：{{sex}}</li>
        <li>爱好：{{hobby}}</li>
        <li>注册时间：{{`${createTime.getFullYear()}-${createTime.getMonth()+1}-${createTime.getDate()}`}}</li>
        <li>QQ邮箱：{{email}}</li>
        <li>座右铭：{{motto}}</li>
      </ul>
    </div>
    <h3>最想说的话：</h3>
    <p>{{say}}</p>
    <span><img src="../../assets/icon/edit.png"
           alt="">
      <h2 @click="$router.push('editUserinfo')">编辑个人信息</h2>
    </span>
  </div>
</template>

<script>
export default {
  data () {
    return {
      photo: require('../../assets/photo.jpeg'),
      username: null,
      sex: null,
      hobby: null,
      createTime: null,
      email: null,
      motto: null,
      say: null
    }
  },
  created () {
    if (window.sessionStorage.getItem('userinfo')) {
      const user = new Map(JSON.parse(window.sessionStorage.getItem('userinfo')))
      if (user.get('photo') !== null) {
        this.photo = user.get('photo') === '' || user.get('photo') === undefined || user.get('photo') === null ? this.photo : user.get('photo')
      }
      this.username = user.get('username')
      this.sex = user.get('sex')
      this.hobby = user.get('hobby')
      this.createTime = new Date(user.get('createTime'))
      this.email = user.get('email')
      this.motto = user.get('motto')
      this.say = user.get('say')
    }
  }
}
</script>

<style scoped>
.vistingCard {
  padding: 0;
  position: relative;
}
.vistingCard img {
  width: 240px;
  height: 240px;
  display: inline-block;
}
.vistingCard ul {
  display: inline-block;
  position: absolute;
  margin: auto;
}
.vistingCard ul li {
  line-height: 40px;
}
p {
  max-width: 1000px;
}
span img,
span h2 {
  display: inline-block;
  cursor: pointer;
}
span img {
  width: 25px;
}
@media only screen and (max-width: 420px) {
  .vistingCard img {
    display: block;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin: auto;
  }
  .vistingCard ul {
    display: block;
    margin: auto;
    position: relative;
    padding: 0;
  }
  .vistingCard ul li {
    list-style: none;
    font-weight: bolder;
  }
}
</style>
