<template>
  <div id="selectInput">
    <input list="options"
           required
           v-model="content"
           @change="handlerulg">
    <datalist id="options">
      <option>JavaScript</option>
      <option>vue</option>
      <option>ES6</option>
      <option>Java</option>
      <option>node</option>
    </datalist>
    <label>{{name}}</label>
    <common-hint :hint="hint"
                 :show="ifShow"></common-hint>
  </div>
</template>

<script>
import commonHint from '../commonHints/commonHint.vue'
export default {
  components: { commonHint },
  props: ['name', 'rule', 'hint', 'createContent'],
  data () {
    return {
      content: '',
      oldContent: '',
      ifShow: 0
    }
  },
  methods: {
    handlerulg () {
      const rue = new RegExp(this.rule)
      if (rue.test(this.content) || this.content === '') {
        this.$emit('inputChange', this.content)
        this.oldContent = this.content
      } else {
        this.ifShow++
        this.content = this.oldContent
      }
    }
  },
  watch: {
    createContent () {
      this.content = this.createContent
      this.oldContent = this.createContent
    }
  }
}
</script>

<style scoped>
#selectInput {
  position: relative;
}
#selectInput input {
  border: none;
  border-bottom: black solid 2px;
  width: 200px;
  height: 40px;
  outline: none;
  background: none;
  position: relative;
}
#selectInput label {
  position: absolute;
  left: 0;
  top: 10px;
  transition: all 0.5s;
  font-weight: bolder;
  font-size: 1rem;
  -webkit-user-select: none;
  color: rgb(122, 120, 120);
}
#selectInput input:focus ~ label,
#selectInput input:valid ~ label {
  top: -10px;
  opacity: 0;
}
</style>
