<template>
  <div class="sonArchive">
    <h3 @click="divShow()">-----{{month[0]}}月</h3>
    <div v-show="isShow">
      <sonarchive-item :title="item"
                       v-for="(item, index) in month[1]"
                       :key="index"
                       v-on="$listeners"
                       v-bind="$attrs"></sonarchive-item>
    </div>
  </div>
</template>

<script>
import sonarchiveItem from './sonarchiveItem.vue'
export default {
  components: { sonarchiveItem },
  props: ['month'],
  data () {
    return {
      isShow: false
    }
  },
  methods: {
    divShow () {
      this.isShow = !this.isShow
    }
  }
}
</script>

<style scoped>
.sonArchive {
  margin-left: 50px;
  box-shadow: -5px 0px 0 aqua;
  padding: 10px 0;
}
h3:hover {
  color: orange;
  cursor: pointer;
}
</style>
