<template>
  <div id="office">
    <text-input name='标题'
                hint='标题字数限制在3-50字之间'
                rule='^.{3,50}$'
                :createContent="blogInformation.title"
                @inputChange="res => blogInformation.title = res"></text-input>
    <select-input name='标签'
                  hint='标签字数限制在3-12字之间'
                  rule='^.{3,12}$'
                  :createContent="blogInformation.tag"
                  @inputChange="res => blogInformation.tag = res"></select-input>
    <textarea-input name='简单介绍一下这一篇博客吧'
                    hint='请将字数控制在3-600字之间'
                    rule='^.{3,600}$'
                    :createContent="blogInformation.introduction"
                    @inputChange="res => blogInformation.introduction = res"></textarea-input>
    <mavon-editor :toolbars="toolbars"
                  ref=md
                  :ishljs="true"
                  :codeStyle="'vs2015'"
                  v-model="editorContent"
                  @imgAdd="$imgAdd"
                  @imgDel="$imgDel"
                  @save="saveMavon"
                  style="min-height:55vh;max-height:80vh;z-index:1;margin-top:25px" />
    <person-center-btn name='保存编辑' @Submit="save"></person-center-btn>
    <common-hint :hint="hint"
                 :show="showHint"></common-hint>
  </div>
</template>

<script>
import textInput from '../commonInput/textInput.vue'
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import SelectInput from '../commonInput/selectInput.vue'
import TextareaInput from '../commonInput/textareaInput.vue'
import PersonCenterBtn from '../commonBtn/personCenterBtn.vue'
import { officeBlog, queryBlogByTag } from '../../api/blog'
import CommonHint from '../commonHints/commonHint.vue'
import { createFile, delFile } from '../../api/_file'
export default {

  components: { textInput, mavonEditor, SelectInput, TextareaInput, PersonCenterBtn, CommonHint },
  data () {
    return {
      editorContent: '',
      hint: null,
      showHint: 0,
      blogInformation: {
        title: null,
        tag: null,
        introduction: null
      },
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: false, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true // 预览
      }
    }
  },
  methods: {
    saveMavon (value, render) {
      if (this.blogInformation.title === null) {
        this.hint = '保存失败，请先填写好标题'
        this.showHint++
      } else {
        officeBlog(this.blogInformation, render, value).then(res => {
          window.sessionStorage.setItem('officeBlogId', res.id)
          this.hint = '保存成功'
          this.showHint++
        })
      }
    },
    $imgAdd (pos, $file) {
      const data = { base64: $file.miniurl.split(',')[1].replace('-', '+').replace('_', '/') }
      const file = createFile($file.name, data)
      file.save().then(file => {
        this.$refs.md.$img2Url(pos, file.attributes.url)
      })
    },
    $imgDel (pos) {
      delFile(pos[0].replace('http://lc-uzUuUouk.cn-n1.lcfile.com/', ''))
    },
    save () {
      if (this.blogInformation.title === null) {
        this.hint = '保存失败，请先填写好标题'
        this.showHint++
      } else {
        officeBlog(this.blogInformation, this.$refs.md.d_render, this.$refs.md.d_value).then(res => {
          window.sessionStorage.setItem('officeBlogId', res.id)
          this.hint = '保存成功'
          this.showHint++
        })
      }
    }
  },
  created () {
    if (window.sessionStorage.getItem('officeBlogId')) {
      queryBlogByTag(window.sessionStorage.getItem('officeBlogId')).then(res => {
        this.blogInformation.title = res.attributes.title
        this.blogInformation.tag = res.attributes.tags
        this.blogInformation.introduction = res.attributes.introduction
        this.editorContent = res.attributes.value
      })
    }
  },
  beforeDestroy () {
    window.sessionStorage.removeItem('officeBlogId')
  }
}
</script>

<style scoped>
</style>
