<template>
  <transition name="fade">
    <div id="personCenterNav" v-show="openPersonCentorNav">
      <div class="userInformation">
        <img :src="photo"
             alt="">
      </div>
      <div class="navigation">
        <router-link to="/home"
                     tag="li"><img src="../../assets/icon/home.png"
               alt=""> 去首页</router-link>
        <router-link to="userinfo"
                     tag="li"><img src="../../assets/icon/user.png"
               alt="">个人信息</router-link>
        <div @click="blogShow = !blogShow">博客
          <div v-show="blogShow"
               @click.stop="">
            <router-link to="office"
                         tag="li"><img src="../../assets/icon/blog.png"
                   alt="">编辑博客</router-link>
            <router-link to="allBlog"
                         tag="li"><img src="../../assets/icon/blog.png"
                   alt="">全部博客</router-link>
            <router-link to="unfinishBlog"
                         tag="li"><img src="../../assets/icon/blog.png"
                   alt="">待完成</router-link>
          </div>
        </div>
        <div @click="DiaryShow = !DiaryShow">日记
          <div v-show="DiaryShow"
               @click.stop="">
            <router-link to="writeDiary"
                         tag="li"><img src="../../assets/icon/diary.png"
                   alt="">写日记</router-link>
            <router-link to="allDiary"
                         tag="li"><img src="../../assets/icon/diary.png"
                   alt="">全部日记</router-link>
          </div>
        </div>
        <router-link to="news"
                     tag="li"><img src="../../assets/icon/comment.png"
               alt="">消息</router-link>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  props: ['openPersonCentorNav'],
  data () {
    return {
      blogShow: false,
      DiaryShow: false,
      photo: require('../../assets/photo.jpeg')
    }
  },
  created () {
    if (window.sessionStorage.getItem('userinfo')) {
      const a = new Map(JSON.parse(window.sessionStorage.getItem('userinfo'))).get('photo')
      this.photo = a === '' || a === undefined || a === null ? this.photo : a
    }
  }
}
</script>

<style scoped>
#personCenterNav {
  height: 100vh;
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  background: white;
  overflow: scroll;
}
.userInformation img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  display: block;
  margin: 25px auto;
}
.navigation li,
.navigation div {
  margin: 0 15px;
  padding-left: 20px;
  list-style: none;
  line-height: 40px;
  border-top: black solid 1px;
  cursor: pointer;
  -webkit-user-select: none;
}
.navigation div div {
  margin: 0;
  padding: 0;
  border: none;
}
.navigation div li {
  border: none;
}
.navigation li:hover {
  color: tomato;
}
.router-link-active {
  color: tomato;
}
img {
  width: 20px;
}
@media only screen and (max-width: 420px) {
  #personCenterNav {
    width: 100vw;
    z-index: 2;
  }
  .fade-enter-active,
  .fade-leave-active {
    transition: all 0.3s linear;
  }
  .fade-enter,
  .fade-leave-to {
    transform: translateX(-100vw);
  }
  .fade-enter-to,
  .fade-leave {
    transform: translateX(0px);
  }
}
</style>
