</template>

<script>
export default {
  props: ['show'],
  data () {
    return {
      etcPhoto: require('../../assets/photo.jpeg')
    }
  },
  created () {
    const a = new Map(JSON.parse(window.sessionStorage.getItem('etc'))).get('photo')
    if (a) {
