<template>
  <div id="blog">
    <h1 @click="goBlog(blogInformation.id)">{{blogInformation.attributes.title}}</h1>
    <span><img src="../../assets/icon/calendar.png" alt="">{{`${blogInformation.createdAt.getFullYear()}-${blogInformation.createdAt.getMonth()+1}-${blogInformation.createdAt.getDate()}`}}</span>
    <span><img src="../../assets/icon/tag.png" alt="">{{blogInformation.attributes.tags}}</span>
    <p>{{blogInformation.attributes.introduction}}</p>
    <div class="popularity">
      <span><img src="../../assets/icon/like.png" alt="">{{blogInformation.attributes.like.length}}</span>
      <span><img src="../../assets/icon/look.png" alt="">{{blogInformation.attributes.pageView}}</span>
      <span><img src="../../assets/icon/comment.png" alt="">{{blogInformation.attributes.comment}}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: ['blogInformation'],
  methods: {
    goBlog (blogId) {
      window.sessionStorage.setItem('blogId', blogId)
      this.$router.push('/blogContent')
    }
  }
}
</script>

<style scoped>
#blog {
  width: calc(100% - 80px);
  background: white;
  margin-bottom: 10px;
  padding: 25px 40px;
  display: inline-block;
}
h1,p{
  margin-top: 5px;
  margin-bottom: 10px;
}
h1:hover {
  cursor: pointer;
  color: tomato;
}
p {
  font-size: 22px;
  cursor: default;
}
.popularity {
  height: 30px;
}
span {
  height: 30px;
  display: inline-block;
  margin-right: 30px;
  font-size: 20px;
  cursor: default;
}
img {
  height: 30px;
  margin-right: 10px;
}

@media only screen and (max-width: 420px){
  #blog {
    width: calc(100% - 40px);
    padding: 25px 20px;
    margin: 0;
    border-bottom: gray dashed 2px ;
  }
}
</style>
