<template>
  <div id="writeDiary">
    <div class="nowDay"><img src="../../assets/icon/calendar.png" alt="">{{newDate}}</div>
    <mavon-editor :toolbars="toolbars"
                  ref=md
                  :ishljs="true"
                  :codeStyle="'vs2015'"
                  @imgAdd="$imgAdd"
                  @imgDel="$imgDel"
                  style="min-height:55vh;max-height:55vh;z-index:1;margin-top:25px" />
    <person-center-btn name='发布日记'
                       @Submit="issueDiary"></person-center-btn>
                       <common-hint :hint="hint" :show="showHint"></common-hint>
  </div>
</template>

<script>
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import { officeDiary } from '../../api/diary'
import { createFile, delFile } from '../../api/_file'
import PersonCenterBtn from '../commonBtn/personCenterBtn.vue'
import CommonHint from '../commonHints/commonHint.vue'
export default {
  components: { mavonEditor, PersonCenterBtn, CommonHint },
  data () {
    return {
      hint: null,
      newDate: null,
      showHint: 0,
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: false, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true // 预览
      }
    }
  },
  methods: {
    $imgAdd (pos, $file) {
      const data = { base64: $file.miniurl.split(',')[1].replace('-', '+').replace('_', '/') }
      const file = createFile($file.name, data)
      file.save().then(file => {
        this.$refs.md.$img2Url(pos, file.attributes.url)
      })
    },
    $imgDel (pos) {
      delFile(pos[0].replace('http://lc-uzUuUouk.cn-n1.lcfile.com/', ''))
    },
    issueDiary () {
      if (this.$refs.md.d_value.replace().length !== 0) {
        officeDiary(this.$refs.md.d_render).then(res => {
          this.$router.push('/diary')
        }, error => {
          if (error) {
            this.hint = '发布失败，请重新登录'
            this.showHint++
          }
        })
      } else {
        this.hint = '日记内容不允许为空'
        this.showHint++
      }
    }
  },
  created () {
    const date = new Date()
    this.newDate = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
  }
}
</script>

<style scoped>
.nowDay {
  font-size: 25px;
}
.nowDay img{
  height: 30px;
  margin-right: 20px;
}
</style>
