<template>
  <div id="sonCommentList">
    <h4 @click.stop="goTalk">{{sonComment.attributes.talker}} @ {{hearerName}}</h4>
    <p class="day">#{{`${sonComment.createdAt.getFullYear()}-${sonComment.createdAt.getMonth()+1}-${sonComment.createdAt.getDate()}`}}</p>
    <div v-html="sonComment.attributes.content"></div>
    <son-comment :sonComment="item"
                 :hearerName="sonComment.attributes.talker"
                 v-for="(item, index) in sonComment.attributes.child"
                 :key="index"></son-comment>
  </div>
</template>

<script>
export default {
  name: 'sonComment',
  props: ['sonComment', 'hearerName'],
  methods: {
    goTalk () {
      this.resetSetItem('hearerId', this.sonComment.id)
      this.resetSetItem('hearerName', this.sonComment.attributes.talker)
    }
  }
}
</script>

<style scoped>
#sonCommentList .day {
  margin: 0;
  color: rgb(75, 74, 74);
}
#sonCommentList h4 {
  color: tomato;
  margin: 0;
  margin-top: 5px;
  cursor: pointer;
}
#sonCommentList h4:hover {
  color: red;
}
</style>
