<template>
  <div id="app">
    <transition name="fade">
      <div v-if="isShow">
        <div id="background"></div>
        <photo @personCenter="res => isShow = res"></photo>
        <common-header></common-header>
      </div>
    </transition>
    <transition name="fade">
      <router-view class="view"
                   v-if="showView"
                   @leavePersonCenter="res => isShow = res"></router-view>
    </transition>
  </div>
</template>

<script>
import CommonHeader from './components/commonHeader/commonHeader'
import Photo from './components/photo.vue'
import { queryToEmail } from './api/userinfo'
export default {
  name: 'App',
  components: {
    CommonHeader,
    Photo
  },
  data () {
    return {
      transitionName: '',
      isShow: true,
      showView: false
    }
  },
  provide () {
    return {
      refresh: this.refresh
    }
  },
  methods: {
    refresh () {
      this.showView = false
      this.$nextTick(function () {
        this.showView = true
      })
    }
  },
  watch: {
    $route (to, from) {
      if (to.path.indexOf('personCenter') !== -1) {
        this.isShow = false
      }
    }
  },
  created () {
    if (this.$route.path.indexOf('personCenter') !== -1) {
      this.isShow = false
    }
    queryToEmail('2564465541@qq.com').then(res => {
      window.sessionStorage.setItem('etc', JSON.stringify([...res]))
      this.showView = true
    })
  }
}
</script>
<style scoped>
#background {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-image: url("./assets/background.jpeg");
  background-repeat: no-repeat;
  background-position: center;
}
.view {
  width: 100%;
  position: absolute;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 1s linear;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}

@media only screen and (max-width: 420px) {
  #background {
    background: rgb(199, 199, 199);
  }
}
</style>
